@import '../common/variables.scss';
@import '../common/media.scss';
// @import '../fonts/style.scss';

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  color: inherit;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}


ul,
ol {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3,
h4,
p {
  color: #34303D;
  margin: 0;
}


::-webkit-scrollbar {
  display: none;
  width: 4px;
  height: 4px;
  border-radius: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #D9D9D9;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(52, 48, 61, 0.15);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(52, 48, 61, 0.15);
  ;
}

*:focus {
  outline: 0;
  outline: none;
}



.scroll-lock {
  overflow: hidden;
}