$tablet-bp: 660px;
$desktop-bp: 479px;


@mixin tablet() {
  @media (min-width: #{$tablet-bp}) {
    @content;
  }
}

@mixin tablet-only() {
  @media (min-width: #{$tablet-bp}) and (max-width: #{$desktop-bp}) {
    @content;
  }
}

@mixin mobile() {
  @media (max-width: 479px) {
    @content;
  }
}

@mixin desktop() {
  @media (min-width: #{$desktop-bp}) {
    @content;
  }
}