@import 'src/styles/core';

.input {
  position: relative;
  width: 100%;
  // border: 2px solid #FFF;
}

.inputWrapper {
  height: 50px;
}

.input-error {
  label[class^='input_label'] {
    top: 18px;
  }
}

.label {
  position: absolute;
  z-index: 1;
  left: 16px;
  padding: 0 5px;
  font-size: 0.8rem;
  font-weight: normal;
  line-height: 14px;
  color: gray !important;
  transition: $transition ease all;

  span {
    margin-left: 5px;
  }

  &_disabled {
    color: gray !important;
  }
}

.success {
  &_container {
    position: absolute;
    top: 15px;
    right: 20px;
    pointer-events: none;
  }

  width: 10px;
}

.phone {
  position: absolute;
  left: 20px;
  top: 16px;
  color: #34303D;
  background-color: #FFFDFB;
}

.label-error {
  color: #C73636 !important;
}

.mask {
  width: calc(100% - 2.8rem);
  padding: 16px;
  color: #34303D;
  border: 1px solid #99979E;
  border-radius: $border-radius;
  outline: none;
  background-color: #FFFDFB;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    margin: 0;
    -webkit-appearance: none;
  }

  &[type='number'] {
    -moz-appearance: textfield;
  }

  &-disabled {
    opacity: 0.5;
  }
}

.mask-error {
  border: 2px solid #C73636;
}

// Input disabled
.disabled {
  opacity: 0.5;

  @media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
    opacity: 1;
  }

  @media only screen and (device-width: 390px) and (device-height: 844px) and (-webkit-device-pixel-ratio: 3) {
    opacity: 1;
  }

  @media only screen and (device-width: 428px) and (device-height: 926px) and (-webkit-device-pixel-ratio: 3) {
    opacity: 1;
  }
}

// Input focused
.focused {
  .mask {
    border-color: #99979E !important;
    box-shadow: 0 0 0 1px #99979E;
  }

  .label {
    color: #99979E;
  }
}

// Input sizes
.medium {
  .mask {
    width: 100%;
    width: -webkit-fill-available;
    height: -webkit-fill-available;
    padding: 10px 10px 10px 20px;
    box-sizing: content-box;
    -moz-box-sizing: border-box;
    font-size: 16px;
  }

  .label {
    top: calc(50% - 0.3rem);
    font-size: 0.8rem;

    @-moz-document url-prefix() {
      top: calc(40% - 0.3rem);
    }
  }

  .error {
    /* errors are not absolute tho */
    top: calc(100% + 6px);
    // margin-top: 5px;
    font-size: 12px;
    line-height: normal;
  }

  .error-sign {
    @include size(20px);

    top: 15px;
    font-size: 12px;
  }

  &.has-value,
  &.focused {
    .label {
      top: -7px;

      left: 12px;

      font-size: 12px;

      color: #99979E !important;

      background-color: #FFF;

      &-error {
        color: #C73636 !important;
      }

      &_disabled {
        z-index: 9 !important;

        color: #99979E !important;

        background-color: #FFF;
      }
    }

    &_disable {
      .disabled {
        opacity: 1;
      }

      input {
        border-color: #99979E !important;
      }
    }

    .mask {
      border-color: #99979E;
    }

    .mask-error {
      border: 2px solid #C73636 !important;

      box-shadow: none;
    }
  }
}

.large {
  .mask {
    height: 58px;
    font-size: 18px;
  }

  .label {
    top: calc(50% - 0.4rem);
    font-size: 0.8rem;
  }

  .error {
    top: calc(100% + 7px);
    font-size: 14px;
  }

  .error-sign {
    @include size(24px);

    top: calc(50% - 12px);
    font-size: 16px;
  }

  &.has-value,
  &.focused {
    .label {
      top: -7px;
      left: 12px;
      font-size: 14px;
      color: #99979E !important;
      background-color: #FFF;
    }

    .mask {
      border-color: #99979E;
    }
  }
}

// Error
.error {
  // position: absolute;
  // left: 15px;
  display: block;
  // margin-top: 4px;
  color: #C73636;
}

.error-sign {
  @include flex(center, center);

  position: absolute;
  right: 20px;

  color: $white;
  background-color: #C73636;
  border-radius: 100px;

  // @media (max-width: 425px) {
  //   top: -58px;
  //   left: 275px;
  // }
}

.suggestions {
  position: absolute;
  z-index: 50;
  top: calc(100% - 12px);
  left: -1px;
  width: calc(100% + 2px);
  max-height: 180px;
  padding-top: 16px;
  overflow-y: auto;
  background: #fff;
  border: thin solid #99979E;
  border-top: none;
  border-radius: 0 0 16px 16px;

  &>* {
    &:last-child {
      border-radius: 0 0 16px 16px;
    }
  }
}

.suggestion {
  padding: 16px 22px;
  font-size: 14px;
  background: #fff;
  transition: 0.25s;
  cursor: pointer;

  &:hover {
    background: #eee;
  }
}