@import 'src/styles/core';

.app {
    // @include desktop {
    //     width: 375px;
    //     margin: 0 auto;

    // }

    overflow-x: hidden;
}


.desctop {
    display: none;
}

.mob {
    display: block;
}

.button {
    margin-top: 30px;
    width: 315px;

    @include mobile () {
        margin-top: 20px;
    }
}

.hiddeDesck {
    display: none !important;
}

.displayMob {
    display: block !important;
}

@media (min-width: 475px) {
    .mob {
        display: none;
        width: 375px;
        margin: 0 auto;
    }


    .desctop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100vh;
        background: #00D1D2;
        background-blend-mode: color-dodge;
        color: #fff;
        font-style: normal;
        letter-spacing: 0;
        text-align: center;


        h1 {
            margin: 0;
            width: 603px;
            font-size: 50px;
            font-style: normal;
            font-weight: 700;
            line-height: 140%;
            color: #fff;

            @include mobile () {
                width: 315px;
                font-size: 32px;
                font-style: normal;
                font-weight: 700;
                line-height: 130%;
            }
        }

        .description {
            margin: 0 auto;
            margin-top: 10px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 130%;

            @include mobile () {
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 130%;
            }
        }

        p {
            width: 426px;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 130%;
            margin-bottom: 78px;
            color: #fff;
            text-align: center;

            @include mobile () {
                width: 315px;
                margin-bottom: 18px;
                font-size: 10px;
                font-style: normal;
                font-weight: 400;
                line-height: 120%;
            }
        }
    }

}

.loader {
    position: fixed;

    z-index: 999;

    top: 0;

    bottom: 0;

    left: 0;

    right: 0;

    display: none;

    background-color: rgba(255, 255, 255, 0.7);

    &:after {
        @include size(30px);

        content: '';

        position: absolute;

        z-index: 1;

        top: 50%;

        left: 50%;

        display: block;

        margin-left: -0.4rem;

        margin-top: -0.4rem;

        padding: 0;

        background: transparent;

        border: 0.1rem solid $dark-grey;

        border-right-color: transparent;

        border-top-color: transparent;

        border-radius: 50%;

        opacity: 1;

        animation: spin 500ms infinite linear;
    }
}

.loader-loading {
    display: block;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}