@import '../core.scss';


#onetrust-banner-sdk {
  padding: 25px 0 25px 0 !important;
  background-color: #34303d !important;

  @include mobile() {
    padding: 0 !important;
  }

  .ot-sdk-row {
    @include desktop() {
      display: flex !important;
    }
  }

  #onetrust-policy {
    @include desktop() {
      margin: 0 0 0 2em !important;
    }
  }

  #onetrust-policy-text {
    font-size: 10px !important;
    font-style: normal !important;
    font-weight: 400 !important;
    line-height: 16px !important;
    letter-spacing: 0 !important;
    text-align: center;
    color: $white !important;

    @include mobile() {
      text-align: center !important;
    }

    a {
      position: relative;
      margin: 0 !important;
      font-size: 10px !important;
      font-style: normal !important;
      font-weight: 400 !important;
      line-height: 16px !important;
      letter-spacing: 0 !important;
      color: $white !important;
      background-color: transparent;
      background-image: linear-gradient(to right, #00d1d2 50%, #34303d 50%);
      background-position: 100% bottom;
      background-repeat: no-repeat;
      background-size: 200% 1px;
      border: none;
      transition: background-position 0.5s ease-in-out;
      // cursor: pointer;
      outline: none !important;
    }

    a:hover {
      background-position: 0% bottom;
    }
  }
}



#onetrust-policy {
  @include mobile() {
    margin-top: 10px !important;
    margin-bottom: 15px !important;
  }
}

#onetrust-policy-text {
  font-size: 10px !important;
}

#onetrust-button-group {
  @include mobile() {
    display: grid !important;
    column-gap: 15px;
    grid-template-columns: 1fr 1fr;
  }

  button {
    position: relative !important;
    z-index: 1 !important;
    height: 45px !important;
    margin: 0 !important;
    overflow: hidden !important;
    font-size: 16px !important;
    font-style: normal !important;
    font-weight: 400 !important;
    line-height: 20px !important;
    letter-spacing: 0 !important;
    text-align: left !important;
    text-decoration: none !important;
    color: $milk-white !important;
    background-color: $dark-grey !important;
    border: 2px solid #f6f4f0 !important;
    border-radius: 50px !important;
    opacity: 1 !important;
    transition: background 0.3s ease-in, color $transition,
      box-shadow 0.3s ease-out !important;
    cursor: pointer !important;
    outline: none !important;

    @include mobile {
      margin: 0 !important;
      margin-bottom: 20px !important;
      text-align: center !important;
    }

    &:first-of-type {
      margin-right: 7.5px !important;
      padding: 9px 28px !important;
    }

    &:last-of-type {
      margin-left: 7.5px !important;
      padding: 11px 20px !important;
      color: $dark-grey !important;
      background-color: #f6f4f0 !important;
      border: none !important;

      @include mobile {
        margin: 0 !important;
        margin-bottom: 20px !important;
      }
    }

    &:after {
      content: ' ' !important;
      position: absolute !important;
      z-index: -1 !important;
      top: 50% !important;
      left: 0 !important;
      transform: translate(-101%, -50%) skew(0deg) !important;
      width: 120% !important;
      height: 120% !important;
      background-color: $aqua-blue !important;
      transition: transform 0.25s ease-out !important;
    }

    &:active,
    &:hover {
      color: #34303d !important;
      border: none !important;
      box-shadow: inset 0 0 0 2px transparent !important;
      --box-shadow-color: transparent !important;

      &:first-of-type {
        padding: 11px 30px !important;
      }

      &:after {
        -webkit-transform: translate3d(-10%, -50%, 0) skew(-30deg) !important;
        transform: translate(-10%, -50%) skew(-30deg) !important;
      }
    }
  }
}

.ot-pc-logo {
  display: none !important;
}

